    </sTitle>
    <div class="outbox">
      <div class="inbox" style="height: 400px;align-items: center;display: flex;flex-direction: column;">
        <el-button-group><el-button @click="show1=false" :disabled="show1===false" type="primary" icon="el-icon-close">收起</el-button>
       <el-button @click="show1=true" :disabled="show1===true" type="primary">打开<i class="el-icon-check el-icon--right"></i></el-button></el-button-group>
       <transition name="el-zoom-in-top">
          <div class="imgBox" v-show="show1">
          <img  style="width: 60%; height: 90%;" src="../../assets/demo1.jpg">
        </div>
        </transition>
        </div>

    </div>
    <sTitle>
      <template #title>按钮加载demo</template>
      <template #introduce>数据还在请求中时实现按钮加载效果</template>
    </sTitle>
