      <div class="inbox">
        <el-row :gutter="20" type="flex"   justify="space-between">
      <el-col :span="6"><div class="grid-content2"><span>您的验证码为:</span></div></el-col>
      <el-col :span="6"><div class="grid-content2"><img v-show="codeUrl!==''" style="width: 100%;height: 110%;" :src="codeUrl"></div> </el-col>
      <el-col :span="6"><div class="grid-content2"><el-button style="font-size: 20px;" @click="getCode" type="primary" :loading="codeUrl===''">获取验证码</el-button></div> </el-col>
