</template>

<script>
import request from '@/utils/request'
export default {
  name: 'ButtonIndex',
  data () {
    return {
      show1: true,
      codeUrl: ''
    }
  },
  async created () {
    const { data: { base64 } } = await request.get('/captcha/image')
    this.codeUrl = base64
  },
  methods: {
    async getCode () {
      this.codeUrl = ''
      const { data: { base64 } } = await request.get('/captcha/image')
      this.codeUrl = base64
      console.log(base64)
    }
  }
}
</script>

